body {
  font-family: sans-serif;
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  background: #f5f5f5;
}

.board-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.board {
  display: grid;
  grid-template-columns: repeat(8, 30px);
  gap: 2px;
  margin: 10px;
}

.cell {
  width: 30px;
  height: 30px;
  background: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-weight: bold;
  user-select: none;
}

.cell.revealed {
  background: #eee;
}

.cell.bomb {
  background: red;
  color: white;
}